.showtopic{
  padding: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid #ccc;
}
.jiazai{
  width: 100%;
}
.showtopic>img{
  width: 30px;
  height: 30px;
  border-radius: 5px;
  margin-right: 10px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.showtopic>.topic-info{
  width: 85px;
  text-align: center;
  margin-right: 5px;
  -ms-flex-negative:  0;
      flex-shrink:  0;
}
.showtopic .topic-tab{
  padding: 3px;
  border-radius: 2px;
  font-size: 12px;
  background-color: #e5e5e5;
  color: #999;
  margin-right: 10px;
  -ms-flex-negative:  0;
      flex-shrink:  0;
}
.showtopic .topic-tab.active{
  background-color: #80bd01;
  color: white;
}
.showtopic .topic-title{
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
}
.showtopic .topic-title a{
  text-decoration: none;
  color: #333;
}
.showtopic .topic-title a:visited{
  color: #888;
}
.showtopic .topic-title a:hover{
  text-decoration: underline;
}



